@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600|Source+Sans+Pro:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600|Source+Sans+Pro:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600|Source+Sans+Pro:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600|Source+Sans+Pro:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#App {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  background: rgb(1,120,131);
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(1,120,131,1)), color-stop(28%, rgba(49,203,217,1)), color-stop(28%, rgba(245,245,245,1)), color-stop(28%, rgba(245,245,245,1)));
  background: linear-gradient(180deg, rgba(1,120,131,1) 0%, rgba(49,203,217,1) 28%, rgba(245,245,245,1) 28%, rgba(245,245,245,1) 28%);
}

#movie-directory{
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  height: 100%;
  width: 100%;
}

#movie-directory h1{
  color: rgb(243, 243, 243);
  font-size: 45px;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  line-height: 80px;
  padding-top: 20px;
}

header h3{
  color: rgb(255, 208, 0);
  margin-top: -20px;
  font-size: 15px;
}

nav{
  font-family: 'Source Sans Pro', sans-serif;
  color: rgb(102, 82, 63);
  text-align: right;
}

a{
  text-decoration: none;
  color: white;
}

header {
  width: 80%;
  height: 18%;
  margin-top: 20px;
  margin-bottom: 60px;
}

.movie-list{
  width: 80%;
  height: 50%;
  overflow-y: scroll;
}

table {
  font-family: 'Source Sans Pro', sans-serif;
  border-collapse: collapse;
  width: 100%;
  background-color: #f5f5f5;
  table-layout: fixed;
  border-spacing: 0;
}

td, th {
  text-align: left;
  width: 210px;
}

td {
  padding: 20px 0px;
  border: 1px dashed #dddddd;
  border-right: none;
  border-left: none;
  border-top: none;
}

tbody tr{
  display: block;
  overflow: auto;
  width: 100%;
}

tbody tr:first-child {
  margin-top: 50px;
}

th{
  border-top: none;
  padding: 10px;
  border-left: none;
  padding-left: 60px;
  font-size: 20px;
  border-bottom: 1px solid #dddddd;

}

th:first-child {
  width: 600px;
}

th:nth-child(2){
  padding-left: 110px;
  width: 600px;
 
}

th:nth-child(3){
  padding-left: 50px;
  width: 500px;
}

thead tr{
  display: block;
  position: absolute;
  background-color: #f5f5f5;
  color: rgb(12, 143, 148);
  width: 80%;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  z-index: 999999999999;
}

td:last-child {
  border: none;
  background-color: hsla(89, 43%, 51%, 0)
}

td:nth-child(3) {
  border-right: none;
}

td:first-child  a{
  color: rgb(10, 151, 161);
  font-weight: bold;
  text-decoration: underline;
}

td:first-child {
  border-left: none;
  width: 36.5%;
  padding-left: 60px;
  overflow-wrap: break-word;
  word-wrap: break-word;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
  word-break: break-all;
}

td:nth-child(2) {
  text-align: left;
  width: 29%;
  padding-left: 25px;
  color: #414141;
}

td:nth-child(3) {
  text-align: left;
  width: 20%;
  padding-left: 25px;
  color: #414141;
}

td:last-child {
  padding-left: 30px;
}

button {
  border: none;
  background-color: hsla(89, 43%, 51%, 0)
}

.options-button:hover{
  cursor: pointer;
}

svg:hover, li:hover, ul:hover{
  cursor: default;
}

li {
  margin-left: -10px;
}

.nav-icon, .options-icon {
  position: relative;
  top: 3px;

}

.options-icon.two {
  top: 0px;
}

.search-box{
  height: 5%;
  width: 80%;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
  margin-top: -20px;
}

.search {
  margin: 0 auto;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  width: 60%;
}

.search input {
  width: 80%;
  padding: 13px 13px;
  box-shadow: 0px 3px 8px 1px rgba(0,0,0,0.3);
  border: none;
}

.search-icon{
  position: relative;
  top: 2px;
  right: 40px;
}
.form-box {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    width: 80%;
    margin-top: 30px;
    height: 60%;
}

form {
    display: flex;
    flex-flow: column wrap;
    width: 80%;
    align-items: flex-start;
}

.input-box {
    padding: 10px;
    margin: 5px 0px;
    border: 1px dashed #dddddd;
    width: 100%;
    border-radius: 10px;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 14px;
}

.input-box.desc {
    height: 80px;
    border: 1px dashed #dddddd;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 14px;
}

label {
    margin-bottom: 0px;
    margin-top: 15px;
    color: rgb(107, 107, 107);
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
}

.rating-choice {
    margin-top: 10px;
    margin-left: 10px;
}

.submit-button {
    width: 150px;
    padding: 10px;
    background-color: rgb(12, 143, 148);
    align-self: flex-end;
    border-radius: 50px;
    font-size: 13px;
    margin-top: 20px;
    text-transform: uppercase;
    color: white;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
}

.submit-button:hover {
    cursor: pointer;
}

.inputValue {
    align-self: flex-end;
    font-size: 12px;
}
.confirmation, .warning{
    width: 80%;
    margin-top: -40px;
    margin-bottom: 20px;
}

.confirmation h2 {
    text-align: right;
    color: green;
    margin-bottom: 60px;
    margin-top: -20px;
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
}

.confirmation-title {
    font-size: 20px;
    margin-bottom: 10px;
    font-family: 'Montserrat', sans-serif;
    color: rgba(1,120,131,1);
}

.confirmation h5{
    margin-top: 20px;
    font-family: 'Montserrat', sans-serif;
    color: #414141;
    display: flex;
    flex-flow: row wrap;
}

.confirmation p {
    font-family: 'Source Sans Pro', sans-serif;
    color: #414141;
}

.warning p{
    text-align: right;
    color: rgb(204, 0, 0);
    margin-bottom: -10px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    margin-top: -20px;
    font-size: 18px;

}

.confirmation h5 span {
    margin-left: 20px;
}

.nav-icon {
    position: relative;
    top: 5px;
}

.warning p:first-child {
    padding-bottom: 15px;
}

.warning p:last-child {
    font-size: 15px;
}



.content {
    width: 70%;
    display: flex;
    flex-flow: column wrap;
    align-items: flex-start;
    margin-top: 50px;
    background-color: rgb(236, 236, 236);
    padding: 20px;
    border-radius: 10px;
    position: relative;
}

h3 {
    align-self: flex-start;
}

p{
    line-height: 25px;
}

.content span {
    margin-left: 10px;
    padding-left: 20px;
}

.content .option-button {
    width: 40%;
    color: #737373;

}

.options-icon2.two {
    position: relative;
    top: 4px;
  }

.content h2 {
    margin-top: 0px;
    margin-bottom: 10px;
    font-size: 30px;
    font-family: 'Montserrat', sans-serif;
    color: orangered;
    padding-left: 20px;
}

.content-dir {
    font-family: 'Source Sans Pro', sans-serif;
    color: #414141;
    font-size: 20px;
    padding-left: 20px;
    display: flex;
    flex-flow: row wrap;
    margin-bottom: 10px;
    margin-top: 0;
}

.content-dir span {
    margin-left: 0px;
    position: relative;
    top: 6px;
}

.content p {
    margin-top: 10px;
    font-family: 'Source Sans Pro', sans-serif;
    color: #414141;
    padding-left: 20px;
    margin-bottom: 30px;
}

.buttons{
    align-self: flex-end;
    display: flex;
    flex-flow: row wrap;
    padding-left: 16px;
    position: absolute;
    bottom: -15px;
}

.buttons-line{
    width: 91%;
    height: 5px;
    background-color: rgb(228, 228, 228);
    margin-right: 10px;
    border-radius: 10px;
    padding: 0px;
    margin-left: 0px;
}

.buttons-info{
    background-color: red;
    padding: 10px;
    margin-right: 10px;
    color: white;
    border-radius: 10px;
    width: 100px;
}

.buttons-info.edit a{
    margin-left: 10px;
}

.buttons-info.delete{
    background-color: rgb(211, 0, 0);
}

.buttons-info.edit {
    background-color: rgb(253, 156, 37);
}

.buttons-info:hover {
    cursor: pointer;
    -webkit-filter: saturate(80%);
            filter: saturate(80%);
}


